export const OnSendTgBuySeasonTicket = async (userData) => {
  // console.log(userData);
  try {
    const urlInsta = `https://www.instagram.com/${userData.user.instagram}/`;
    let seasonTicketKind = '';
    if (userData.seasonTicket.kind === 'group') {
      seasonTicketKind = '';
    }
    if (userData.seasonTicket.kind === 'personal') {
      seasonTicketKind = '';
    }

    const phoneNumber = userData.user.tel.replace(/\D/g, ''); //   ,  
    const nationalPhoneNumber = phoneNumber.startsWith('38') ? phoneNumber.slice(2) : phoneNumber; //  +38,   

    const res = await tgSandra.get(` ${userData.user.name} ${userData.user.surname}     ${userData.seasonTicket.name}  ${seasonTicketKind}   ${userData.seasonTicket.price} .   : ${nationalPhoneNumber}, instagram: ${urlInsta}`);

    return res;
  } catch (error) {
    toast.error('   ');
    console.log(error.message)
  }
};


export const OnRecordTraining = async (userData) => {
  // console.log(userData)
  try {
    const res = await serverAPI.post(`/user/signUpTraining`, userData);
    // console.log(res)
    if (res.data.messages.error && res.data.messages.error === '    .      ') {
        toast.warning(`${res.data.messages.error}`);
      return res;
    } else if (res.data.messages.error === '   ') {
        toast.error('   ');
      return res.data;
    } else {
        toast.success('    ');
      return res;
    }
  } catch (e) {
    toast.error('   ');
    console.log(e);
  }
};

